.text-center {
  text-align: center;
}

.bg-primary {
  background-color: #84d9bc;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*,
*:before,
*:after {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

/* HEADER SECTION */
.header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../images/header.jpg");
  background-repeat: no-repeat;
  background-position: top center;
  background-size: cover;
}
.header__logo {
  max-width: 20rem;
  padding: 5rem 0;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .header__logo {
    margin: 0;
  }
}
.header__content {
  padding: 13rem 0 7rem;
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .header__content {
    align-items: flex-start;
  }
}
@media (min-width: 1024px) {
  .header__content {
    padding: 15rem 0;
  }
}
.header__heading {
  color: #FFFFFF;
  font-size: 4.5rem;
  text-align: center;
}
@media (min-width: 768px) {
  .header__heading {
    text-align: left;
    font-size: 5rem;
    max-width: 50%;
  }
}
@media (min-width: 1024px) {
  .header__heading {
    font-size: 6rem;
    max-width: 40%;
  }
}
.header__button {
  margin-top: 1rem;
  background-color: #84d9bc;
  font-size: 2rem;
  padding: 1.2rem 4rem;
  font-weight: 700;
  display: block;
  width: 100%;
  text-align: center;
  color: #FFFFFF;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border: none;
  font-size: 2.5rem;
}
.header__button:hover {
  cursor: pointer;
  background-color: #5dcda7;
}
@media (min-width: 768px) {
  .header__button {
    width: auto;
    display: inline-block;
  }
}

.about {
  padding: 5rem 0;
}
@media (min-width: 768px) {
  .about {
    padding: 8rem 0;
  }
}
@media (min-width: 768px) {
  .about__grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    align-items: center;
  }
}
.about__heading {
  margin: 5rem 0 3rem;
  color: #313c4e;
  display: inline-block;
  font-size: 3.6rem;
}
.about__heading::after {
  content: "";
  display: block;
  height: 1rem;
  margin-top: -1.4rem;
  width: calc(100% + 1rem);
  background-color: #84d9bc;
}